import { Routes, RouterModule } from '@angular/router';  import { EnderecoDadosComponent }   from './endereco_dados.component'; import { EnderecoDadosDetailsComponent } from './endereco_dadosDetails.component'; import { EnderecoDadosEditComponent } from './endereco_dadosEdit.component';  const endereco_dados_routes: Routes = [   {     path: '',     component: EnderecoDadosComponent,     children: [       { path:'details', component: EnderecoDadosDetailsComponent },       { path:'edit', component: EnderecoDadosEditComponent }     ]   } ];  export const endereco_dados_routing = RouterModule.forChild(endereco_dados_routes); 


