import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'endereco-dados',   templateUrl: 'endereco_dados.component.html' }) export class EnderecoDadosComponent implements OnInit {       displayMode: EnderecoDadosDisplayModeEnum;     displayModeEnum = EnderecoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = EnderecoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = EnderecoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = EnderecoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum EnderecoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


