import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Endereco } from '../../../modelo/Endereco'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'endereco-dados-details',   templateUrl: 'endereco_dadosDetails.component.html' }) export class EnderecoDadosDetailsComponent implements OnInit {    endereco: Endereco;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.endereco.Endereco";                 let id = +params['id'];         this.endereco = new Endereco();         this.endereco.cd_endereco = id;         o.objetoJson = this.endereco;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a endereco. '; }      this.endereco = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


